#root{
  width: 100%;
  min-height: 100vh;
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  background: rgb(42, 41, 51)
}
body{
  overflow: hidden;
}
.slider{
  display: flex;
  flex-direction: column;
}
.view{
  border-radius: 0.4rem;
  display: flex;
  padding: 1rem;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  margin: 0.8rem;
  height: calc(100vh - 3.5rem);
  background: rgb(27, 25, 35);
  color: white;
}
.buttonsHolder{
  position: absolute;
  bottom: 0;
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
}
.skinBtn{
  width: 1.5vw;
  height: 1.5vw;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  border: none;
  border-radius: 0.2rem;
  color: white;
  margin: 0 0.2rem;
}
.infinite-scroll-component__outerdiv{
  display: flex;
  justify-content: center;
  width: 100%;
}
.infiniteScroll{
  width: 100%;
  overflow: unset !important;
  height: unset !important;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.infiniteScroll::-webkit-scrollbar {
  width: 0px;
}
*{
  outline: none;
}
.series{
  width: 30vw;
  border-radius: 0.3rem;
  margin: 1rem;
  overflow: hidden;
  margin-top: 0;
}
.searchInput{
  display: flex;
  border: none;
  flex: 1;
  padding-left: 1rem;
  height: 2rem;
}
.searchBtn{
  width: 20%;
  border: none;
  height: 2rem;
  border-left: solid 2px gray;
  cursor: pointer;
}
.searchBtn:active{
  background-color: #32cd32 !important;
}
.skinBtn:active{
  background-color: white !important;
  color: black;
}
.skinsHolder{
  display: flex;
  width: 70%;
  padding: 0.5rem;
  max-height: 80vh;
  overflow: scroll;
  box-sizing: border-box;
  justify-content: space-around;
  flex-wrap: wrap;
  background: rgb(42, 41, 51);
  border-radius: 0.5rem;
  overflow-x: hidden;
  margin-right: -8px;
}
.skinWrapper{
  position: relative;
}

.skinImg{
  width: 10vw;
  height: 10vw;
  border-radius: 50%;
  margin: 0.2rem;
}
.hidden{
  visibility: hidden;
  pointer-events: none;
}
.navbar{
  padding: 0;

}
.navItem{
  margin: 0.5rem;
  border-radius: 0.2rem;
  border: none;
  height: 1.8rem;
  flex: 1;
  color: white;
  font-weight: bold;
  max-width: 200px;
  cursor: pointer;
}
.navItem:active{
  filter: brightness(150%);
}
.skinText{
  background-color: rgba(25, 25, 25, 0.72);
  position: absolute;
  font-size: 1.2rem;
  text-align: center;
  width: 100%;
}

@media (orientation:portrait){
  .skinsHolder{
    width: 100%;
  }
  .series{
    width: 70vw;
  }
  .skinImg{
    width: 20vw;
    height: 20vw;
  }
  .skinBtn{
    opacity: 0.8;
    width: 5vw;
    height: 5vw;
  }
}